<template lang="pug">
// 
v-card
    v-card-title.d-flex.justify-center 
    v-card-text
        v-form(v-model='editAccount.valid' ref='form')
            v-text-field.mb-4(v-model='editAccount.name' label='' name='name' :rules='editAccount.nameRules' required='required' large='large')
            v-select.no-border(label='' v-model='editAccount.type' prepend-icon='mdi-domain' :items='editAccount.typeList' item-text='name' item-value='idx' flat)
            v-select.no-border(label='' v-model='editAccount.industry' prepend-icon='mdi-domain' :items='editAccount.industryList' item-text='name' item-value='idx' flat)
            v-file-input.no-border(prepend-icon='mdi-image' label='LOGO' accept='image/*')
            v-autocomplete.no-border(v-model='editAccount.own' :items='editAccount.ownList' item-text='name' item-value='idx' prepend-icon='mdi-account' label='')
            v-text-field.no-border(v-model='editAccount.web' prepend-icon='mdi-earth' label='')
            v-row(no-gutters='no-gutters')
                v-col(cols='6')
                    v-text-field.no-border(v-model='editAccount.cell' prepend-icon='mdi-phone' label='')
                v-col(cols='6')
                    v-text-field.no-border(v-model='editAccount.fax' prepend-icon='mdi-fax' label='')
            v-text-field.no-border(v-model='editAccount.address' prepend-icon='mdi-map' label='')
            v-text-field.no-border(v-model='editAccount.peoples' prepend-icon='mdi-account-group' label='')
    v-card-actions.px-2.py-4
        v-row
            v-col.pr-2(cols='6')
                v-btn(:ripple='false' @click='onCancel' width='100%' height='40' color='cancel' depressed dark) 
            v-col.pl-2(cols='6')
                v-btn(:ripple='false' @click='onConfirm' width='100%' height='40' color='primary' depressed dark) 
</template>

<script>
	import Vue from 'vue';

	export default Vue.extend({
        props: ['accountData'],
		data() {
			return {
				editAccount: {
                    displayStatus: false,
                    valid: true,
                    logo: '',
                    name: '',
                    nameRules: [v => !!v || ''],
                    type: '',
                    typeList: ['','Analyst','Competitor','Customer','Other'],
                    industry: '',
                    industryList: [
                        { idx: 1, name: '' },
                        { idx: 2, name: '' }
                    ],
                    own: '',
                    ownList: [
                        { idx: 1, name: 'James Campbell' },
                        { idx: 2, name: 'Andy Lu' }
                    ],
                    web: '',
                    cell: '',
                    fax: '',
                    peoples: '',
                    address: '',
                },
			}
		},
        mounted() {
            this.editAccount.name = this.accountData.name;
            this.editAccount.logo = this.accountData.logo;
            this.editAccount.type = this.accountData.type;
            this.editAccount.industry = this.accountData.industry;
            this.editAccount.cell = this.accountData.cell;
            this.editAccount.fax = this.accountData.fax;
            this.editAccount.address = this.accountData.address;
            this.editAccount.own = this.accountData.own;
            this.editAccount.web = this.accountData.web;
            this.editAccount.peoples = this.accountData.peoples;
        },
        methods: {
			validate() {
				this.$refs.form.validate()
			},
			onConfirm() {
				this.$emit('emitEditAccountDialog', true);
			},
			onCancel() {
				this.$emit('emitEditAccountDialog', false);
			},
		}
	});
</script>