						v-icon(size='14' v-else) icon-arrow-btm
					v-stepper.process-box(v-model='processStep' :class="isProcessComplete ? 'complete':''")
						v-stepper-header.process-bar(v-if='isProcessLost' :class="isProcessLost ? 'lost':''")
							v-stepper-step
								span LOST
						v-stepper-header.process-bar(v-else :class="isProcessWon ? 'won':''")
