											.d-flex.align-center
												h4.my-2 
												v-btn.ml-1(v-if='showEditKeywordBtn' icon color='gary' @click='editKeyword(item)')
													v-icon(size='20') icon-edit
												.d-flex.align-center.ml-auto(v-if='showEditKeywordCheckBtn')
													v-btn(width='70' text color='gary' @click='cancelEditKeyword(item, idx)')
														v-icon(size='20') icon-cancel
														span 
													v-btn.ml-3(width='70' text color='success' @click='confirmEditKeyword()')
														v-icon(size='20') icon-check
														span 
											p.d-flex.align-center.input-has-label.dance
												label(:for='`${index}-finishDate`') 
												v-text-field(v-model='item.stepFinishDate' :id='`${index}-finishDate`' solo='solo' dense='dense' :readonly='isReadonlyKeyword')
											p.d-flex.align-center.input-has-label.dance
												label(:for='`${index}-describe`') 
												v-text-field(v-model='item.describe' :id='`${index}-describe`' solo='solo' dense='dense' :readonly='isReadonlyKeyword')
											p.d-flex.align-center.input-has-label.dance
